/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 10px;
}

.img {
    width: 100px;
    /* background-color: #f2f2f2; */
}

.left-side {
    flex: 1;
}

.right-side {
    flex: 1;
    display: flex;
    justify-content: flex-end;
}

.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav-links li {
    display: inline-block;
    margin-left: 20px;
}

.nav-links li:first-child {
    margin-left: 0;
}

.nav-links a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
}

.nav-links a:hover {
    color: #555;
}

.logo {
    font-size: 50px;
    font-weight: 300;
    /* color: rgba(235, 215, 17, 0.8); */


}

