.carousel {
    display: flex;
    overflow: hidden;
}

.slide {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 300px;
    /* Set an appropriate height for your carousel */
}

.image {
    flex: 1;
}

.image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.text {
    flex: 2;
    padding: 20px;
}